.videos {
    width: 100%;
    height: 100%;
    &__name {
        display: flex;
        align-items: center;
        padding-bottom: 20px;
        &-img {
            width: 35px;
            padding-right: 10px;
        }
        &-text {
            font-family: Mulish;
            font-weight: 700;
            font-size: 20px;
        }
    }
    &-channel {
        display: flex;
        overflow-x: scroll;
        &::-webkit-scrollbar {
            display: none;
        }
    }
    &__recommended {
        &-title {
            margin-top: 20px;
            margin-bottom: 15px;
        }
        &-videos {
            display: flex;
            overflow-x: scroll;
            &::-webkit-scrollbar {
                display: none;
            }
            &-item {
                position: relative;
                width: 270px;
                height: 125px;
                background-color: #fff;
                border-radius: 14px;
            }
        }
    }
    &__foods {
        margin-top: 7px;
        &-top {
            display: flex;
            align-items: center;
            &-img {
                width: 40px;
            }
            &-name {
                margin: 0 19px;
            }
            &-rec {
                font-family: Mulish-Light;
                font-size: 14px;
                font-weight: 400;
                line-height: 20px;
                color: #C2C2C2;
            }
        }
        &-bottom {
            display: flex;
            width: 100%;
            overflow-x: scroll;
            &::-webkit-scrollbar {
                display: none;
            }
        }
    }
}