@use './Colors.scss';
@font-face {
    font-family: Mulish;
    src: url('../assets/fonts/static/Mulish-Black.ttf');
}

@font-face {
    font-family: Mulish-Light;
    src: url('../assets/fonts/static/Mulish-Light.ttf');
}

* {
    padding: 0;
    margin: 0;
    font-family: Mulish;
    color: white;
    outline: none;
}

.App {
    background-color: Colors.$main-dark;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    &__content {
        padding: 20px 0 40px 30px;
    }
}