@use './Colors.scss';
.page {
    width: 100%;
    height: 100%;
    &__main {
        // box-sizing: content-box;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: space-between;
        &-frame {
            min-height: 480px;
            min-width: 1080px;
            width: 100%;
            // height: 90%;
        }
        &-text {
            padding-bottom: 15px;
            border-bottom: 1px solid #D8D8D880;
            margin-bottom: 20px;
            &-others {
                & * {
                    color: #717176;
                    font-family: Mulish-Light;
                }
                display: flex;
                align-items: center;
                justify-content: space-between;
                &-buttons {
                    display: flex;
                    align-items: center;
                    &-button {
                        display: inline-block;
                        // padding: 10px;
                        background-color: Colors.$main-grey;
                        margin-left: 10px;
                        border-radius: 20px;
                        padding: 8px 16px;
                        & svg {
                            margin-right: 8px;
                            display: inline-block;
                        }
                    }
                    &-dots {
                        display: flex;
                        padding: 0;
                        padding-bottom: 10%;
                        justify-content: space-between;
                        margin-left: 30px;
                        &-item {
                            width: 0;
                            height: 0;
                            margin-left: 10px;
                        }
                    }
                }
            }
        }
        &-foods {
            width: 100%;
            &-top {
                display: flex;
                justify-content: space-between;
                // align-items: center;
                &-left {
                    display: flex;
                    align-items: flex-start;
                    & img {
                        margin-right: 10px;
                        // height: 100%;
                        // width: 100%;
                        // padding: 0;
                    }
                    &-time {
                        font-size: 13px;
                        font-family: Mulish-Light;
                        opacity: .4;
                        margin-bottom: 7px;
                    }
                    &-text {
                        width: 40vw;
                        font-size: 13px;
                        line-height: 20px;
                        font-family: Mulish-Light;
                        opacity: .6;
                        margin-bottom: 5px;
                    }
                    &-more {
                        font-family: Mulish-Light;
                        font-size: 15px;
                        opacity: .6;
                    }
                }
                &-right {
                    &-subs {
                        background: Colors.$main-red;
                        padding: 10px 20px;
                        border-radius: 30px;
                        cursor: pointer;
                        font-family: Mulish-Light;
                    }
                }
            }
        }
    }
    &__right {
        margin-right: 50px;
        &-top {
            display: flex;
            justify-content: space-between;
            align-items: center;
            &-right {
                display: flex;
                align-items: center;
                &-play {
                    font-size: 13px;
                }
            }
        }
        &-videos {
            &-item {
                display: block;
                padding: 0;
            }
        }
    }
}

.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
}


/* Hide default HTML checkbox */

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
}

.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
}

input#switchBox:not(:checked)+.slider {
    background-color: #555;
}

input:checked+.slider {
    background-color: #2196F3;
}

input:focus+.slider {
    box-shadow: 0 0 1px #2196F3;
}

input:checked+.slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
}


/* Rounded sliders */

.switch {
    transform: scale(.7);
    padding: 0;
}

.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}

.my-1 {
    margin-top: .25rem!important;
    margin-bottom: .25rem!important
}

.mx-3 {
    margin-right: 1rem!important;
    margin-left: 1rem!important
}