@use "./Colors.scss";
.channel {
    box-sizing: content-box;
    &__banner {
        height: 200px;
        width: 100%;
        overflow: hidden;
    }
    &__user {
        display: flex;
        align-items: center;
        margin-top: 15px;
        &-subs {
            margin-left: auto;
            margin-right: 15vw;
            background: Colors.$main-red;
            padding: 10px 20px;
            border-radius: 30px;
            cursor: pointer;
        }
        &-data {
            margin-left: 10px;
            &-subs {
                font-weight: 400;
                font-size: 12px;
                color: Colors.$main-grey;
            }
        }
    }
    &__links {
        display: flex;
        list-style-type: none;
        &-item {
            margin-top: 15px;
            &-link {
                margin-right: 35px;
                font-size: 16px;
                text-decoration: none;
                color: Colors.$main-grey;
                &-active {
                    text-decoration: underline;
                    color: Colors.$main-red;
                }
            }
        }
    }
    &__main {
        max-width: 650px;
        margin-top: 30px;
        display: flex;
        &-video {
            width: 270px;
            height: 150px;
        }
        &-desc,
        &-views {
            margin-top: 10px;
            font-size: 14px;
            font-family: Munish-Light;
            // color: Colors.$main-grey;
            color: white;
            opacity: .6;
        }
        &-out {
            width: 100%;
            display: flex;
            justify-content: space-between;
            &-right {
                margin-right: 15vw;
                &-name {
                    color: #C2C2C2;
                }
                &-links {
                    &-item {
                        text-decoration: none;
                        display: flex;
                        align-items: center;
                        margin-top: 10px;
                        &-img {
                            width: 40px;
                            margin-right: 15px;
                        }
                        &-name {
                            font-family: Mulish-Light;
                            font-weight: 700;
                            font-size: 17px;
                        }
                    }
                }
            }
        }
    }
    &__bottom {
        &-name {
            margin-top: 20px;
            margin-bottom: 15px;
        }
        &-videos {
            display: flex;
            width: 100%;
            overflow-x: scroll;
            &::-webkit-scrollbar {
                display: none;
            }
            &-item {
                width: 150px;
            }
        }
    }
}

.df {
    display: flex;
    align-items: center;
}