@use './Colors.scss';
.videocard {
    display: inline-block;
    padding-right: 30px;
    &-img {
        position: relative;
        width: 210px;
        height: 100px;
        background-color: #fff;
        border-radius: 14px;
        &-time {
            font-family: Mulish-Light;
            font-size: 13px;
            position: absolute;
            right: 12px;
            bottom: 12px;
            background-color: grey;
            opacity: 0.8;
            padding: 3px 10px;
            border-radius: 15px;
        }
    }
    &__top {
        padding-top: 10px;
        padding-bottom: 4px;
        &-hidden {
            display: none;
        }
    }
    &__bottom {
        display: flex;
        justify-content: space-between;
        align-items: center;
        &-left {
            display: flex;
        }
        &-left,
        &-views,
        &-times,
        &-author,
        & pre {
            font-family: Mulish-Light;
            font-weight: 400;
            font-size: 12px;
            color: #515156;
        }
        &-hidden {
            display: none;
        }
    }
}