@use './Colors.scss';
.navbar {
    width: 85%;
    box-sizing: border-box;
    align-items: flex-start;
    display: inline-flex;
    padding-bottom: 20px;
    &__search {
        margin-right: 10%;
        width: 70%;
        display: flex;
        &-input {
            position: relative;
            align-items: center;
            width: 100%;
            height: 22px;
            padding: auto;
            background-color: Colors.$main-grey;
            border-radius: 15px;
            padding: 17px;
            border-width: 0;
            font-family: Mulish-Light;
            font-size: 16px;
            font-weight: 400;
            &::after {
                content: url('../assets/images/Search.png');
                display: inline-block;
                // position: absolute;
                // background-image: ;
                // right: 100px;
                // height: 18px;
                // width: 18px;
                background-color: #fff;
            }
        }
    }
    &__others {
        display: flex;
        align-items: center;
        &-item {
            margin: 0 25px;
            height: 20px;
        }
        &-accaunt {
            width: 40px;
            height: 40px;
            margin-left: 25px;
            border-radius: 20px;
            margin-right: 50px;
        }
    }
}