@use "./Colors.scss";
.sidebar {
    height: 100vh;
    margin-right: 60px;
    // padding-right: 50px;
    box-sizing: content-box;
    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__burger {
        display: block;
        &-item {
            margin: 4px 0;
            display: block;
            width: 18px;
            height: 1px;
            background: white;
            border: 1px solid white;
            border-radius: 5px;
        }
    }
    &__logo {
        display: flex;
        align-items: center;
        &-img {
            margin-left: 38px;
            width: 37px;
            height: 25px;
            margin-right: 4px;
        }
        &-hidden {
            display: none;
        }
    }
    &__links {
        padding-top: 10px;
        list-style-type: none;
        &-item {
            margin-top: 12px;
            margin-bottom: 12px;
            &-link {
                font-family: Mulish-Light;
                font-weight: 400;
                font-size: 14px;
                color: #8B8B8E;
                text-decoration: none;
                & svg {
                    margin-right: 6px;
                    & path {
                        opacity: 1;
                        fill: #8B8B8E;
                    }
                }
                &-active {
                    & svg path {
                        fill: #FF0000;
                    }
                }
            }
        }
        &-hidden {
            display: none;
        }
    }
    &__subs {
        list-style: none;
        &-item {
            margin-top: 10px;
            &-link {
                display: flex;
                align-items: center;
                font-family: Mulish-Light;
                font-weight: 400;
                font-size: 12px;
                color: #8B8B8E;
                text-decoration: none;
            }
            &-img {
                width: 26px;
                margin-right: 5px;
            }
        }
        &-hidden {
            display: none;
        }
    }
}